import React, { useRef } from "react";
import CopyButton from "../../components/shared/CopyButton";

function AdmissionResult({ values }) {
  const aResultRef = useRef();
  const isPremature = values.numberField < 37;
  const isPostTerm = values.numberField >= 40;
  const term = isPremature ? "PRETERMINO" : "A TERMINO";
  const planFields = ["oxygen", "diet", "liquid", "drugs", "nurse"];
  const planFieldsSS = ["test", "images", "consult"];

  return (
    <>
      <CopyButton resultRef={aResultRef} />
      <div ref={aResultRef}>
        <h1>
          {" "}
          NOTA DE INGRESO A UNIDAD DE CUIDADOS {values.careType}S NEONATALES
        </h1>
        <p style={{ fontSize: "0.5rem" }}>
          SE REALIZA VALORACIN CONJUNTA CON PEDIATRA EN TURNO
        </p>
        <h2>HIJO DE {values.kidOf}</h2>
        <h3>DOCUMENTO MATERNO: {values.idMom}</h3>
        <h2>MOTIVO DE CONSULTA</h2>
        <p>
          {" "}
          REMITIDO DE {values.placeofRemission} POR {values.consultMotive}{" "}
        </p>
        <h2>ENFERMEDAD ACTUAL</h2> <p> {values.actualDisease}</p>
        <h2>ANTECEDENTES PERINATALES</h2>
        <p> {values.antecedentesPerinatales}</p>
        <h2> EXAMEN FSICO </h2>
        <p>
          {" "}
          SIGNOS VITALES FC:<strong> {values.cardiacFreq} </strong>LPM  FR:{" "}
          <strong>{values.respiratoryFreq}</strong> RPM  SAT02:{" "}
          <strong>{values.saturation} </strong>% - T:{" "}
          <strong>{values.temperture}C</strong>{" "}
        </p>
        <p>{values.physicalExam}</p>
        <h2>DIAGNSTICOS</h2>
        <p>
          -RECIN NACIDO {term} {values.babyGender} DE {values.numberField}{" "}
          SEMANAS POR BALLARD
        </p>
        <p>{values.diagnosis}</p>
        <h2>ANLISIS </h2>
        <p>
          RECIBO PACIENTE {term} EN REGULARES CONDICIONES GENERALES. SE
          ENCUENTRA PACIENTE ACTIVO REACTIVO, SIN DFICIT APARENTE, NI CRISIS
          NEONATALES,{" "}
          {values.hasOxygen ? (
            <strong> RECIBIENDO OXGENO SUPLEMENTARIO DE BAJO FLUJO </strong>
          ) : (
            <strong>TOLERANDO OXGENO AMBIENTE</strong>
          )}{" "}
          , PATRN RESPIRATORIO ADECUADO, NORMOSATURADO, MANTIENE SIGNOS VITALES
          ESTABLES, NO SOPORTES, BIEN PERFUNDIDO,{" "}
          {values.oralIntake ? (
            <strong> AYUNADO</strong>
          ) : values.foley ? (
            <strong> TOLERANDO APORTE ENTERAL POR SONDA </strong>
          ) : (
            <strong> TOLERANDO APORTE ENTERAL POR SUCCIN </strong>
          )}
          , HIDRATADO, NORMOGLICMICO, NO EDEMAS, {values.paraclinicAnalysis} SE
          INGRESA LA UNIDAD PARA VIGILANCIA ESTRICTA Y MANEJO, PRONSTICO SUJETO
          A EVOLUCIN CLNICA. SE EXPLICA A LOS PADRES QUIENES REFIEREN ENTENDER
          Y ACEPTAR.
        </p>
        <h2> PLAN </h2>
        <p>-HOSPITALIZAR EN CUIDADOS {values.careType}S NEONATALES </p>
        {planFields.map((field) => {
          return (
            <React.Fragment key={field}>
              {values[field] && <p>-{values[field]}</p>}
            </React.Fragment>
          );
        })}
        {planFieldsSS.map((field) => {
          return (
            <React.Fragment key={field}>
              {values[field] && <p>-{values[field]}</p>}
            </React.Fragment>
          );
        })}
        {values.pending && (
          <>
            <h3>*****PENDIENTES******</h3>
            <p>-{values.pending}</p>
          </>
        )}
      </div>
    </>
  );
}
export default AdmissionResult;
